.popup__container {
    width: 430px;
    height: 330px;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    z-index: 2;
    position: relative;
}

@media screen and (max-width:767px) {
    .popup__container {
        width: 282px;
        height: 322px;
    }

}