.profile {
    max-width:880px;
    margin:0 auto 50px auto;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

@media screen and (max-width:767px) {
    .profile {
        flex-direction: column;
        margin:0 auto 36px auto;
    }

}